import { portrait } from '../data/data'

function AboutMe() {
  return (
    <div className="about_me" id="aboutme">
      <section>
        <h1>About me</h1>
        <h3>
          Web developer - passion d'escalade et de volleyball - toujours
          souriant et prt pour de nouvelles aventures
        </h3>
        <p>
          Hello, Je m'appelle Pierre et je suis passionn de code. Ici, vous
          trouverez un aperu de mon parcours, de mes comptences et de mes
          ralisations dans le domaine du dveloppement informatique. Explorez
          mes projets et n'hsitez pas  me contacter pour discuter de
          collaborations passionnantes. Merci de votre visite !
        </p>
      </section>
      <img src={portrait.path} alt={portrait.alt} />
    </div>
  )
}

export default AboutMe
